$primary: #950C06;
$light: #FFF;

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Roboto', sans-serif;
}

.background-primary {
  background-color: $primary;
}

.background-light {
  background-color: $light;
}

.text-primary {
  color: $primary!important;
}

.text-light {
  color: $light;
}
